.header {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 430px;
  background-image: url("../assets/header.png");
  background-repeat: no-repeat;
  background-size: contain;
}

.main_image{
  margin: auto;
  background-image: url("../assets/main.png");
  width: 315px;
  height: 515px;
  background-repeat: no-repeat;
  background-size: contain;
}
.main__leaf{
  bottom: -3%;
  right: 35%;
  position: absolute;
  z-index: -1;
  margin: auto;
  background-image: url("../assets/leaf.png");
  width: 100px;
  height: 200px;
  background-repeat: no-repeat;
  background-size: contain;
}

.nameheader {
  padding-bottom: 20px;
  margin-top: 2%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.nameheader h2 {
  color: #373535;
  font-size: 42px;
  font-family: 'Playfair Display', serif;
  font-weight: normal;
  letter-spacing: 5px;
}

.main__vermelding{
  font-family: 'Playfair Display', serif;
  padding: 0;
  margin: 10px;
}

.nameheader h3 {
  color: #373535;
  font-size: 18px;
  font-family:'Playfair Display', serif;
  font-weight: normal;
  letter-spacing: 5px;
}

.nameheader h2,
h3 {
  padding: 5px 0;
  margin: 0;
}

.main_image {
  position: relative;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.rsvp__container {
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 30px;
}

.app__rsvp {
  background-color: black;
  color: white;
  padding: 10px 20px;
  border-radius: 10px;
  text-decoration: none; /* no underline */
}

.date__container {
  margin-top: 10px;
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.date__date {
  font-size: 32px;
}
.date__date p {
  text-align: right;
}

.date__spacer {
  margin: 0 10px;
  width: 2px;
  color: black;
  background-color: black;
  height: 100px;
}

.date__info {
  font-size: 22px;
}

.date__info p {
  margin: 5px;
}

.vermelding__container{
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main__title{
  font-style: italic;
  font-family:'Playfair Display', serif;
  font-weight: bold;
  letter-spacing: 2px;
  margin: 0;
  padding: 10px 0 0 0;
  font-size: 32px;
}
.text_left{
  text-align: right;
}
.text_right{
  text-align: left;
}
h4{
  margin: 0 !important;
  padding: 0 !important;
}
@media screen and (max-width: 695px) {
  .main_image{
    background-position: center center;
    margin-bottom: 0px;
    background-image: url("../assets/main.png");
    width: 100%;
    height: 450px;
  }
  .nameheader h2 {
    color: #373535;
    font-size: 42px;
    font-family: 'Playfair Display', serif;
    font-weight: normal;
    letter-spacing: 5px;
  }
  .nameheader h3 {
    padding: 0 20px;
    color: #373535;
    font-size: 18px;
    font-family:'Playfair Display', serif;
    font-weight: normal;
    letter-spacing: 5px;
  }
  .main__leaf{
    bottom: 2%;
    right: 2%;
    position: absolute;
    z-index: -1;
    margin: auto;
    background-image: url("../assets/leaf.png");
    width: 100px;
    height: 200px;
    background-repeat: no-repeat;
    background-size: contain;
  }
}
