.venue__container {
  vertical-align: top;
  margin: 0 20px;
  color: #373535;
  text-align: center;
  font-family: "Playfair Display", serif;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: flex-start;
  position: relative;
}
.image_container{
  width: 100%;
  display: flex;
  justify-content: center;
  margin: auto;
}

.chateau_img{
  margin: auto;
  background-image: url("../assets/chateau.png");
  width: 600px;
  height: 300px;
  background-repeat: no-repeat;
  background-size: contain;
}

.venue__leaf{
  background-image: url("../assets/7.png");
  top: 0%;
  left: 25%;
  position: absolute;
  z-index: -1;
  margin: auto;
  width: 80px;
  height: 200px;
  background-repeat: no-repeat;
  background-size: contain;
}
.venue__leaf2{
  background-image: url("../assets/8.png");
  top: 45%;
  left: 65%;
  position: absolute;
  z-index: -1;
  margin: auto;
  width: 300px;
  height: 400px;
  background-repeat: no-repeat;
  background-size: contain;
}

.venue__container h3 {
  padding-top: 3%;
  font-size: 32px;
  font-weight: normal;
}
.venue__container h2 {
  text-align: left;
  margin: 20px 0;
}

.venue__br {
  background-color: #373535;
  height: 2px;
  width: 20%;
}

p{
  text-align: left;
}

.venue__bio {
  max-width: 800px;
  padding: 0 20px 0 0;
}


.rsvp__img {
  background-image: url("../assets/pin.png");
  color: red;
  background-repeat: no-repeat;
  background-size: contain;
  width: 30px;
  height: 30px;
  padding: 0 0 10px 0;
}

.rsvp__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}

.venue__card{
  width: 600px;
  margin: 0 10px 0 0;
}

@media screen and (max-width: 695px) {
  .venue__card{
    width: 100%;
    margin: 0 20px;
  }
  .venue__container {
    color: #373535;
    text-align: center;
    font-family: "Playfair Display", serif;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    position: relative;
  }
  .venue__leaf{
    width: 50px;
    height: 200px;
    background-image: url("../assets/7.png");
    top: 0%;
    left: 15%;
    position: absolute;
  }
  .venue__leaf2{
    background-image: url("../assets/8.png");
    width: 200px;
    height: 300px;
    top: 65%;
    left: 65%;
    position: absolute;
}
}