.info__parent{
    font-family: "Playfair Display", serif;
    color: #373535;
    text-align: center;
    position: absolute;
    width: 100%;
    margin: auto;
    display: flex;
    justify-content: center;
}
.info__container{
    width: 60%;
    margin: auto;
}

.venue__leaf {
    background-image: url("../assets/7.png");
    top: 10%;
    left: 25%;
    position: absolute;
    z-index: -1;
    margin: auto;
    width: 80px;
    height: 200px;
    background-repeat: no-repeat;
    background-size: contain;
  }
  .venue__leaf2 {
    background-image: url("../assets/8.png");
    top: 55%;
    left: 65%;
    position: absolute;
    z-index: -1;
    margin: auto;
    width: 300px;
    height: 400px;
    background-repeat: no-repeat;
    background-size: contain;
  }