.programme__parent {
  font-family: "Playfair Display", serif;
  width: 60%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
}
.programme__container {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 20px;
}
.programme__container h2 {
  padding: 0;
  margin: 0;
}
.programme__seperator {
  margin: 20px;
  width: 10px;
  height: 1px;
  border-bottom: 1px solid black;
}