body {
  height: 100vh;
}

.header {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 430px;
  background-image: url("../assets/header.png");
  background-repeat: no-repeat;
  background-size: contain;
}

.nameheader {
  padding-bottom: 20px;
  margin-top: 2%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.nameheader h2 {
  color: #373535;
  font-size: 42px;
  font-family: "Playfair Display", serif;
  font-weight: normal;
  letter-spacing: 5px;
}

.nameheader h3 {
  color: #373535;
  font-size: 18px;
  font-family: "Playfair Display", serif;
  font-weight: normal;
  letter-spacing: 5px;
}

.nameheader h2,
h3 {
  padding: 5px 0;
  margin: 0;
}

.rsvp__p h2 {
  color: #373535;
  font-size: 22px;
  font-family: "Playfair Display", serif;
  font-weight: normal;
  letter-spacing: 5px;
  padding: 0 20px;
}
.rsvp__p h3 {
  font-family: "Playfair Display", serif;
}
.rsvp__p {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.rsvp__p p {
  font-family: "Playfair Display", serif;
  letter-spacing: 1px;
  text-align: center;
  width: 50%;
}

.parent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form {
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: start;
}

.form input {
  padding: 0 10px;
  border-style: none;
  color: #bfb89d;
  border: 2px solid #bfb89d;
  border-color: #bfb89d;
  margin: 10px 0;
  height: 50px;
}

.form button {
  border-style: none;
  color: white;
  background-color: #bfb89d;
  text-align: center;
  font-weight: bold;
  height: 50px;
}

.rsvp__button {
  margin-top: 30px;
  text-align: center;
  width: 200px;
  background-color: #99b199;
  padding: 20px;
  border-radius: 5px;
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.venue__leaf {
  background-image: url("../assets/7.png");
  top: 50%;
  left: 25%;
  position: absolute;
  z-index: -1;
  margin: auto;
  width: 80px;
  height: 200px;
  background-repeat: no-repeat;
  background-size: contain;
}
.venue__leaf2 {
  background-image: url("../assets/8.png");
  top: 45%;
  left: 65%;
  position: absolute;
  z-index: -1;
  margin: auto;
  width: 300px;
  height: 400px;
  background-repeat: no-repeat;
  background-size: contain;
}

@media screen and (max-width: 695px) {
  .rsvp__p p {
    text-align: center;
    width: 80%;
  }
}
